<template>
<div @click="hd()">
    <div class="diva" v-if="flag">
        <span class="spana"></span>
    </div>
    <div class="divb" v-if="!flag">
        <span class="spanb"></span>
    </div>
</div>
</template>

<script>
export default {
 // 组件名称
 name: 'Zi',
 // 组件参数 接收来自父组件的数据
 props: [],
 // 局部注册的组件
 components: {},
 // 组件状态值
 data () {
 return {
     flag:true,
 }
 },
 // 计算属性
 computed: {},
 // 侦听器
 watch: {},
 // 组件方法
 methods: {
     hd(){
         this.flag=!this.flag
         this.$emit('qq',this.flag)
    }
 },
 /**
 * 组件实例创建完成，属性已绑定，但DOM还未生成，$ el属性还不存在
 */
 created () {
 },
 /**
 /**
 * el 被新创建的 vm.$ el 替换，并挂载到实例上去之后调用该钩子。
 * 如果 root 实例挂载了一个文档内元素，当 mounted 被调用时 vm.$ el 也在文档内。
 */
 mounted () {
 },
 /**
 * 由于数据更改导致的虚拟 DOM 重新渲染和打补丁，在这之后会调用该钩子。
 * 当这个钩子被调用时，组件 DOM 已经更新，所以你现在可以执行依赖于 DOM 的操作。
 */
 updated () {
 },
 /**
 * keep-alive 组件激活时调用。 仅针对keep-alive 组件有效
 */
 activated () {
 },
 /**
 * keep-alive 组件停用时调用。 仅针对keep-alive 组件有效
 */
 deactivated () {
 },
}
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style scoped >
.diva{
    width: 0.6rem;
    height: 0.3rem;
    background: #ccc;
    border-radius: 30%;
}
.divb{
    width: 0.6rem;
    height: 0.3rem;
    background: rgb(6, 243, 45);
    border-radius: 30%;
    position: relative;
}
.spana{
    display: inline-block;
    background: rgb(95, 85, 247);
    height: 0.3rem;
    width: 0.3rem;
    border-radius: 50%;
}
.spanb{
    display: inline-block;
    background: rgb(95, 85, 247);
    height: 0.3rem;
    width: 0.3rem;
    border-radius: 50%;
    position: absolute;
    left: 0.3rem;
}
</style>
