<template>
  <div id="app">
    <header>
      <van-nav-bar
        title="ELE"
        left-text="返回顶部"
        right-text="登录/注册"
        @click-left="onClickLeft"
        @click-right="onClickRight"
        fixed
        v-show="flag"
      />
      <van-nav-bar title left-text="返回顶部" fixed  v-show="!flag" @click-left="onClickLeft" @click-right="grxx()">
        <template #right>
          <van-icon name="friends-o" size="20" />
        </template>
      </van-nav-bar>
    </header>
    <section>
      <nav>
        <p>当前定位城市</p>
        <p>{{dq.name}}</p>
