        <ul id="hot">
          <li class="lic" v-for="(i,k) in hot" :key="k">{{i.name}}</li>
        </ul>
      </div>
      <van-index-bar :sticky-offset-top="46" :index-list="indexList">
        <div v-for="(i,k) in v" :key="k">
          <van-index-anchor :index="k" />
          <ul class="ula">
            <li class="lic" v-for="(it,ke) in i" :key="ke" @click="tzl(it)">{{it.name}}</li>
          </ul>
        </div>
      </van-index-bar>
    </section>
    <router-view></router-view>
  </div>
</template>

<script>
import { Toast } from "vant";
import { cty } from "@/utils/api";
export default {
  data() {
    return {
      dq: [],
      hots: [],
      list: [],
      arr: [],
      arrs: [],
      v: "",
      flag: true,
      idk: JSON.parse(localStorage.getItem("a")) || "",
      indexList: [
        "A",
        "B",
        "C",
        "D",
        "E",
        "F",
        "G",
        "H",
        "J",
        "K",
        "L",
        "M",
        "N",
        "P",
        "Q",
        "R",
        "S",
        "T",
        "W",
        "X",
        "Y",
        "Z"
      ]
    };
  },
  methods: {
    async dqs() {
      let { data } = await cty("group");
      this.list = data;
      console.log(this.list);
      let a = Object.keys(this.list).sort();

      a.map(m => {
        this.arr[m] = this.list[m];
      });
      this.v = { ...this.arr };
      console.log(this.v);
      console.log(this.arr);
    },
    async dqcs() {
      let { data } = await cty("guess");
      this.dq = data;
      console.log(this.dq);
    },
    async hot() {
      let { data } = await cty("hot");
      this.hots = data;
      console.log(this.hots);
    },
    onClickLeft() {
      this.goTop();
    },
    onClickRight() {
      this.$router.push("/About");
    },
    grxx() {
      this.$router.push("/User");
    },
    goTop() {
      // 动画
      var timer = setInterval(function() {
        //获取滚动条到顶部的位置
        let osTop =
          document.documentElement.scrollTop || document.body.scrollTop;
        let ispeed = Math.floor(-osTop / 5);
        // 判断到顶部的等式：滚动条到顶部的位置 + 当前窗口内容可视区 == 滚动条的总高度
        document.documentElement.scrollTop = document.body.scrollTop =
          osTop + ispeed;
        this.isTop = true;
        if (osTop === 0) {
          clearInterval(timer);
        }
      }, 30);
    },
    tzl(k) {
      this.$router.push({
        name: "Xq",
        params: { age: k }
      });
    }
  },
  mounted() {
    this.dqs();
    this.dqcs();
    this.hot();
    if (this.idk == "") {
      this.flag = true;
    } else {
      this.flag = false;
    }
  }
};
</script>

