<template>
  <div>
    <header >
      <van-nav-bar @click-right="grxx()" >
        <template #left>
          <van-icon name="search" size="24" />
        </template>
        <template #title>
          <h5 @click="th()">{{arr.address}}</h5>
        </template>
        <template #right>
          <van-icon name="friends-o" size="24" />
        </template>
      </van-nav-bar>
    </header>
    <section>
      <router-view></router-view>
    </section>
    <footer>
      <van-tabbar v-model="active">
        <van-tabbar-item icon="home-o">外卖</van-tabbar-item>
        <van-tabbar-item icon="search">搜索</van-tabbar-item>
        <van-tabbar-item icon="setting-o">订单</van-tabbar-item>
        <van-tabbar-item icon="friends-o" @click="togo()">我的</van-tabbar-item>
      </van-tabbar>
    </footer>
  </div>
</template>

<script>
export default {
  // 组件名称
  name: "",
  // 组件参数 接收来自父组件的数据
  props: [],
  // 局部注册的组件
  components: {},
  // 组件状态值
  data() {
    return {
      arr: JSON.parse(localStorage.getItem("c")) || [],
      active: 0
    };
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
    th() {
      this.$router.push("/");
    },
    sx() {
      this.$router.push("/Gr");
    },
    grxx() {
      this.$router.push("/User");
    },
    togo(){
        this.$router.push("/User");
    }
  },
  /**
   * 组件实例创建完成，属性已绑定，但DOM还未生成，$ el属性还不存在
   */
  created() {},
  /**
 /**
 * el 被新创建的 vm.$ el 替换，并挂载到实例上去之后调用该钩子。
 * 如果 root 实例挂载了一个文档内元素，当 mounted 被调用时 vm.$ el 也在文档内。
 */
  mounted() {
    if (this.$route.params.age != undefined) {
      this.arr = this.$route.params.age;
      console.log(this.arr);
      localStorage.setItem("c", JSON.stringify(this.arr));
    } else {
      return false;
    }
    this.sx();
  },
  /**
   * 由于数据更改导致的虚拟 DOM 重新渲染和打补丁，在这之后会调用该钩子。
   * 当这个钩子被调用时，组件 DOM 已经更新，所以你现在可以执行依赖于 DOM 的操作。
   */
  updated() {},
  /**
   * keep-alive 组件激活时调用。 仅针对keep-alive 组件有效
   */
  activated() {},
  /**
   * keep-alive 组件停用时调用。 仅针对keep-alive 组件有效
   */
  deactivated() {}
};
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style scoped >
h5 {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
</style>
