<template>
  <div>
    <header>
      <van-nav-bar title="密码登录" left-text="返回" left-arrow @click-left="onClickLeft" />
    </header>
    <section>
      <div id="hz">
        <p id="p2">
          账号：
          <input type="tel" v-model="tel" @blur="Phone()" />
        </p>
        <hr>
        <p id="p">
          <span>密码：
          <input :type="type" v-model="pwd" @blur="mm()" /></span>
          <Zi @qq="qqhh"></Zi>
        </p>
        <hr>
        <p id="p1">
          验证码：
          <input type="number" v-model="yam" @blur="yzm()" />
          <img :src="pic" alt />
          <button class="btn" @click="huan()">换</button>
        </p>
        <hr>
      </div>
      <div id="divz">
        <p>温馨提示：未注册过的账号，登录时将自动注册</p>
        <p>注册过的用户可凭账号密码登录</p>
      </div>
      <van-button type="primary" size="large" :disabled="zt" @click="lgi()">登录</van-button>
    </section>
  </div>
</template>

<script>
import axios from 'axios'
axios.defaults.withCredentials=true;
import Zi from "@/views/Zi";
export default {
  // 组件名称
  name: "About",
  // 组件参数 接收来自父组件的数据
  props: [],
  // 局部注册的组件
  components: { Zi },
  // 组件状态值
  data() {
    return {
      tel: "",
      pwd: "",
      yam: "",
      type: "password",
      zt: true,
      jy: [],
      pic: ""
    };
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {
    jy: {
      handler(val) {
        console.log(val);
        if (val.length == 3) {
          this.zt = false;
          console.log(this.zt);
        } else {
          this.zt = true;
          console.log(this.zt);
        }
      }
    }
  },
  // 组件方法
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    Phone() {
      if (!/^1[3456789]\d{9}$/.test(this.tel)) {
        alert("手机号码有误，请重填");
        if (this.jy != []) {
          this.jy.shift(1);
        }
        return false;
      } else {
        this.jy.push(1);
        console.log(this.jy);
      }
    },
    mm() {
      if (this.pwd == "" || this.pwd.length != 6) {
        alert("密码不能为空或不正确，请重填");
        if (this.jy != []) {
          this.jy.shift(1);
        }
        return false;
      } else {
        this.jy.push(2);
        console.log(this.jy);
      }
    },
    yzm() {
      if (this.yam == "" || this.yam.length != 4) {
        alert("验证码不能为空或不正确，请重填");
        if (this.jy != []) {
          this.jy.shift(1);
        }
        return false;
      } else {
        this.jy.push(3);
        console.log(this.jy);
      }
    },
    qqhh(data) {
      console.log(data);
      if (data) {
        this.type = "password";
      } else {
        this.type = "text";
      }
    },
    lgi() {
      this.setCookie("cap", this.yam, 2);
      this.setCookie("SID", 100, 2);
      this.axios.post("api/v2/login", {
          username: this.tel,
          password: this.pwd,
          captcha_code: this.yam
        })
        .then(res => {
          if(res.data.status==undefined){
            this.$router.push({
              name:"Home",
              params:{}
            })
          }
          localStorage.setItem('a',JSON.stringify(res.data))
        });
    },
    setCookie(key, value, t) {
      var oDate = new Date();
      oDate.setDate(oDate.getDate() + t);
      document.cookie = key + "=" + value + "; expires=" + oDate.toDateString();
    },
    huan(){
      this.$post("http://elm.cangdu.org/v1/captchas").then(res => {
      console.log(res);
      this.pic = res.data.code;
      console.log(this.pic);
    });
    }
  },
  /**
   * 组件实例创建完成，属性已绑定，但DOM还未生成，$ el属性还不存在
   */
  created() {},
  /**
 /**
 * el 被新创建的 vm.$ el 替换，并挂载到实例上去之后调用该钩子。
 * 如果 root 实例挂载了一个文档内元素，当 mounted 被调用时 vm.$ el 也在文档内。
 */
  mounted() {
    this.$post("http://elm.cangdu.org/v1/captchas").then(res => {
      console.log(res);
      this.pic = res.data.code;
      console.log(this.pic);
    });
  },
  /**
   * 由于数据更改导致的虚拟 DOM 重新渲染和打补丁，在这之后会调用该钩子。
   * 当这个钩子被调用时，组件 DOM 已经更新，所以你现在可以执行依赖于 DOM 的操作。
   */
  updated() {},
  /**
   * keep-alive 组件激活时调用。 仅针对keep-alive 组件有效
   */
  activated() {},
  /**
   * keep-alive 组件停用时调用。 仅针对keep-alive 组件有效
   */
  deactivated() {}
};
</script> 

