  border: none;
  background: #ccc;
  height: 0.3rem;
  
}
#p1 {
  display: flex;
  height: 0.3rem;
  padding:  0 0.05rem;
}
#p1 > input {
  border: none;
  background: #ccc;
}
#p2 {
  display: flex;
