import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import About from '../views/About'
import Gr from '@/views/Gr'
import Xq from '@/views/Xq'
import Sy from '@/views/Sy'
import User from '@/views/User'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: '/About',
    name: 'About',
    component: About,
  },
  {
    path: '/Gr',
    name: 'Gr',
    component: Gr,
    children: [
      {
        path: '/',
        name: 'Sy',
        component: Sy,
      },
      
      
    ],
    
  },
  {
    path: '/Xq',
    name: 'Xq',
    component: Xq,
  },
  {
    path: '/User',
    name: 'User',
    component: User,
  }
]

const router = new VueRouter({
  routes
})

export default router
