<template>
  <div>
    <van-swipe class="my-swipe" indicator-color="white" :loop="false">
      <van-swipe-item>
        <van-grid square>
          <div class="box">
            <dl v-for="(i,k) in list" :key="k" v-if="k <8">
              <dt>
                <img :src="pic+i.image_url" alt />
              </dt>
              <dd>{{i.title}}</dd>
            </dl>
          </div>
        </van-grid>
      </van-swipe-item>
      <van-swipe-item>
        <van-grid square>
          <div class="box">
            <template></template>
            <dl v-for="(i,k) in list" :key="k" v-if="k >8">
              <dt>
                <img :src="pic+i.image_url" alt />
              </dt>
              <dd>{{i.title}}</dd>
            </dl>
          </div>
        </van-grid>
      </van-swipe-item>
    </van-swipe>
    <van-card
      v-for="(i,k) in lists"
      :key="k"
      :desc="i.promotion_info"
      :title="i.name"
      tag="品牌"
      :thumb="cpic+i.image_path"
      price="20.00起送"
    >
      <template #tags>
        <van-tag plain type="danger">蜂鸟专递</van-tag>
        <van-tag plain type="danger">准时达</van-tag>
        <span class="span1">20公里/33分钟</span>
      </template>
    </van-card>
  </div>
</template>

<script>
export default {
  // 组件名称
  name: "",
  // 组件参数 接收来自父组件的数据
  props: [],
  // 局部注册的组件
  components: {},
  // 组件状态值
  data() {
    return {
      c: JSON.parse(localStorage.getItem("c")),
      list: [],
      pic: "",
      cpic: "",
      lists: ""
    };
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {},
  /**
   * 组件实例创建完成，属性已绑定，但DOM还未生成，$ el属性还不存在
   */
  created() {},
  /**
 /**
 * el 被新创建的 vm.$ el 替换，并挂载到实例上去之后调用该钩子。
 * 如果 root 实例挂载了一个文档内元素，当 mounted 被调用时 vm.$ el 也在文档内。
 */
  mounted() {
    let a = this.c.latitude;
    let b = this.c.longitude;
    this.pic = "https://fuss10.elemecdn.com";
    this.cpic = "http://elm.cangdu.org/img/";
    this.$get(
      `http://elm.cangdu.org/v2/index_entry?geohash=` +
        a +
        `,` +
        b +
        `&group_type=1&flags[]=F`
    ).then(res => {
      console.log(res);
      this.list = res.data;
      console.log(this.list);
    });
    this.$get(
      `http://elm.cangdu.org/shopping/restaurants?latitude=` +
        a +
        `&longitude=` +
        b +
        `&offset=0&limit=20&extras[]=activities&keyword=&restaurant_category_id=&restaurant_category_ids[]=&order_by=&delivery_mode[]=`
    ).then(res => {
      this.lists = res.data;
      console.log(this.lists);
    });
  },
  /**
   * 由于数据更改导致的虚拟 DOM 重新渲染和打补丁，在这之后会调用该钩子。
   * 当这个钩子被调用时，组件 DOM 已经更新，所以你现在可以执行依赖于 DOM 的操作。
   */
  updated() {},
  /**
   * keep-alive 组件激活时调用。 仅针对keep-alive 组件有效
   */
  activated() {},
  /**
   * keep-alive 组件停用时调用。 仅针对keep-alive 组件有效
   */
  deactivated() {}
};
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style scoped >
.my-swipe .van-swipe-item {
  color: #fff;
  font-size: 0.2rem;
  line-height: 1.5rem;
  text-align: center;
  background-color: #39a9ed;
}
.box {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}
.box > dl {
  width: 25%;
  height: 0.75rem;
}
.box > dl > dt {
  width: 100%;
  height: 0.5rem;
  line-height: 0.5rem;
}
.box > dl > dt > img {
  width: 0.47rem;
  height: 0.47rem;
}
.box > dl > dd {
  width: 100%;
  height: 0.25rem;
  text-align: center;
  line-height: 0.25rem;
  margin: 0;
  padding: 0;
  color: #000;
  font-size: 0.16rem;
}
.my-swipe .van-swipe-item[data-v-6e7c41c0] {
  background: #fff !important;
}
.span1{
    display: inline-block;
    margin-left: 1rem;
}
</style>
