<template>
  <div>
    <header>
      <van-nav-bar title="我的" left-text="返回" left-arrow @click-left="onClickLeft" />
    </header>
    <van-card desc="暂无绑定手机号" :title="a.username" :thumb="tx+a.avatar">
      <template #footer>
        <van-button size="mini" @click="tc()">退出</van-button>
      </template>
    </van-card>
    <nav>
      <div>
        <p>
          <span>0.00</span>元
        </p>
        <p>我的余额</p>
      </div>
      <div>
        <p>
          <span>{{a.gift_amount}}</span>个
        </p>
        <p>我的优惠</p>
      </div>
      <div>
        <p>
          <span>0</span>分
        </p>
        <p>我的积分</p>
      </div>
    </nav>
    <van-tabbar v-model="active">
      <van-tabbar-item icon="home-o" @click="togo()">外卖</van-tabbar-item>
      <van-tabbar-item icon="search">搜索</van-tabbar-item>
      <van-tabbar-item icon="setting-o">订单</van-tabbar-item>
      <van-tabbar-item icon="friends-o">我的</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
export default {
  // 组件名称
  name: "",
  // 组件参数 接收来自父组件的数据
  props: [],
  // 局部注册的组件
  components: {},
  // 组件状态值
  data() {
    return {
      a: JSON.parse(localStorage.getItem("a")),
      tx: "",
      active: 3,
    };
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    tc() {
      localStorage.removeItem("a");
      this.$router.push("/");
    },
    togo(){
        this.$router.push("/Gr");
    }
  },
  /**
   * 组件实例创建完成，属性已绑定，但DOM还未生成，$ el属性还不存在
   */
  created() {},
  /**
 /**
 * el 被新创建的 vm.$ el 替换，并挂载到实例上去之后调用该钩子。
 * 如果 root 实例挂载了一个文档内元素，当 mounted 被调用时 vm.$ el 也在文档内。
 */
  mounted() {
    this.tx = "http://elm.cangdu.org/img/";
  },
  /**
   * 由于数据更改导致的虚拟 DOM 重新渲染和打补丁，在这之后会调用该钩子。
   * 当这个钩子被调用时，组件 DOM 已经更新，所以你现在可以执行依赖于 DOM 的操作。
   */
  updated() {},
  /**
   * keep-alive 组件激活时调用。 仅针对keep-alive 组件有效
   */
  activated() {},
  /**
   * keep-alive 组件停用时调用。 仅针对keep-alive 组件有效
   */
  deactivated() {}
};
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style scoped >
.van-card__title {
  max-height: 0.55rem !important;
  height: 0.55rem !important;
  line-height: 0.55rem !important;
  font-size: 0.25rem !important;
}
.van-card {
  background: rgb(19, 15, 248) !important;
}
.van-card:not(:first-child) {
  margin-top: 0 !important;
}
.van-card__desc,
.van-card {
  color: #fff !important;
}
nav {
  display: flex;
  width: 100%;
  align-items: center;
  height: 1rem;
}
nav > div {
  flex: 1;
  text-align: center;
}
p > span {
  font-size: 0.25rem;
  color: red;
}
</style>
