<template>
  <div>
    <header>
      <van-nav-bar
        :title="arr.name"
        left-text="返回"
        right-text="切换城市"
        left-arrow
        @click-left="onClickLeft"
        @click-right="onClickRight"
      />
      
    </header>
    <section>
      <van-search v-model="value" placeholder="输入学校、商务楼、地址" />
      <van-button type="primary" size="large" @click="tj()">提交</van-button>
    </section>
    <footer>
      <ul v-show="flag">
        <li>历史记录</li>
        <li class="lib" v-for="(i,k) in list" :key="k" @click="govs(i)">
          <p>{{i.name}}</p>
          <span>{{i.address}}</span>
        </li>
      </ul>
      <ul v-show="!flag">
        <li class="lib" v-for="(i,k) in slist" :key="k" @click="gov(k)">
          <p>{{i.name}}</p>
          <span>{{i.address}}</span>
        </li>
      </ul>
    </footer>
  </div>
</template>

<script>
export default {
  // 组件名称
  name: "Xq",
  // 组件参数 接收来自父组件的数据
  props: [],
  // 局部注册的组件
  components: {},
  // 组件状态值
  data() {
    return {
      arr: "",
      value: "",
      list: JSON.parse(localStorage.getItem("b")) || [],
      slist: [],
      flag: true,
    };
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
    onClickLeft() {
      this.$router.push("/");
    },
    onClickRight() {
      this.$router.push("/");
    },
    tj() {
      this.flag = !this.flag;
      this.$get(
        `http://elm.cangdu.org/v1/pois?type=search&city_id=${this.arr.id}&keyword=${this.value}`
      ).then(res => {
        console.log(res.data);
        this.slist = res.data;
      });
    },
    gov(k) {
      if (this.list.length == 5) {
        this.list.shift(1);
        this.list.push(this.slist[k]);
        localStorage.setItem("b", JSON.stringify(this.list));
        this.$router.push({
        name: "Gr",
        params: { age: this.slist[k] }
      });
      } else {
        this.list.push(this.slist[k]);
        localStorage.setItem("b", JSON.stringify(this.list));
        this.$router.push({
        name: "Gr",
        params: { age: this.slist[k] }
      });
      }
    },
    govs(k) {
      console.log(k)
      if (this.list.length == 5) {
        this.list.shift(1);
        this.list.push(k);
        localStorage.setItem("b", JSON.stringify(this.list));
        this.$router.push({
        name: "Gr",
        params: { age: k }
      });
      } else {
        this.list.push(k);
        localStorage.setItem("b", JSON.stringify(this.list));
        this.$router.push({
        name: "Gr",
        params: { age: k }
      });
      }
    }
  },
  /**
   * 组件实例创建完成，属性已绑定，但DOM还未生成，$ el属性还不存在
   */
  created() {},
  /**
 /**
 * el 被新创建的 vm.$ el 替换，并挂载到实例上去之后调用该钩子。
 * 如果 root 实例挂载了一个文档内元素，当 mounted 被调用时 vm.$ el 也在文档内。
 */
  mounted() {
    this.arr = this.$route.params.age;
    console.log(this.arr);
  },
  /**
   * 由于数据更改导致的虚拟 DOM 重新渲染和打补丁，在这之后会调用该钩子。
   * 当这个钩子被调用时，组件 DOM 已经更新，所以你现在可以执行依赖于 DOM 的操作。
   */
  updated() {},
  /**
   * keep-alive 组件激活时调用。 仅针对keep-alive 组件有效
   */
  activated() {},
  /**
   * keep-alive 组件停用时调用。 仅针对keep-alive 组件有效
   */
  deactivated() {}
};
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style scoped >
section {
  padding: 0 0.2rem;
}
.lib {
  width: 100%;
}
.lib > p {
  font-size: 0.2rem;
}
.lib > span {
  font-size: 0.16rem;
  color: #ccc;
}
</style>
